import { useState, useEffect, useRef } from 'react';
import supabaseService from '../services/SupabaseService';

/**
 * Hook base para autenticacin y usuario actual
 *
 * Centraliza la lgica comn de todos los hooks de permisos:
 * - Obtencin del usuario actual desde Supabase
 * - Estado de loading con prevencin de loops infinitos
 * - Verificacin de autenticacin y membresas activas
 * - Deteccin automtica de organizacin
 * - Auto sign-out para usuarios sin membresas
 * - Suscripcin a cambios de autenticacin (SIGNED_IN, SIGNED_OUT)
 *
 * @returns {Object} Estado de autenticacin y funciones de usuario
 * @returns {Object|null} return.currentUser - Objeto de usuario de Supabase (user_metadata, email, id)
 * @returns {string|null} return.organizationId - ID de la organizacin actual del usuario
 * @returns {boolean} return.isLoading - Estado de carga del usuario y organizacin
 * @returns {boolean} return.isAuthenticated - Si el usuario est autenticado (tiene email)
 * @returns {string|null} return.userEmail - Email del usuario actual (helper de currentUser.email)
 * @returns {string|null} return.userId - ID del usuario actual (helper de currentUser.id)
 * @returns {Function} return.reloadUser - Funcin para recargar usuario y organizacin manualmente
 *
 * @example
 * const { currentUser, isAuthenticated, userEmail, organizationId, isLoading } = useAuthUser();
 *
 * if (isLoading) return <Spinner />;
 * if (!isAuthenticated) return <LoginPrompt />;
 *
 * return <div>Bienvenido {userEmail}</div>;
 */
const useAuthUser = () => {
  const [currentUser, setCurrentUser] = useState(null);
  const [organizationId, setOrganizationId] = useState(null);
  const [isLoading, setIsLoading] = useState(true);
  const [isAuthenticated, setIsAuthenticated] = useState(false);

  // Refs para prevenir loops infinitos
  const isLoadingRef = useRef(false);
  const lastUserEmail = useRef(null);

  useEffect(() => {
    const loadUser = async () => {
      // Evitar mltiples ejecuciones simultneas
      if (isLoadingRef.current) {
        console.log(' useAuthUser ya est cargando, evitando duplicacin...');
        return;
      }

      try {
        isLoadingRef.current = true;
        setIsLoading(true);

        // Obtener usuario actual
        const user = supabaseService.getCurrentUser();

        // Si no hay cambio de usuario, no actualizar
        if (user?.email === lastUserEmail.current) {
          return;
        }

        lastUserEmail.current = user?.email || null;

        if (!user || !user.email) {
          console.log(' No hay usuario autenticado');
          setCurrentUser(null);
          setOrganizationId(null);
          setIsAuthenticated(false);
          return;
        }

        console.log(' Usuario autenticado:', user.email);

        // Obtener organizacin actual
        const orgId = supabaseService.getCurrentOrganization();

        if (!orgId) {
          console.log(' Esperando deteccin de organizacin...');

          // Verificar si el usuario tiene membresas activas
          try {
            const { data: userMemberships, error: membershipError } = await supabaseService.supabase
              .from('organization_members')
              .select('id, organization_id, status')
              .eq('user_email', user.email)
              .eq('status', 'active');

            if (membershipError) {
              console.error(' Error verificando membresa:', membershipError);
            } else if (!userMemberships || userMemberships.length === 0) {
              console.log(' USUARIO SIN MEMBRESAS ACTIVAS');
              console.log(' Forzando cierre de sesin para:', user.email);

              // Forzar cierre de sesin
              await supabaseService.supabase.auth.signOut();

              // Limpiar cach local
              localStorage.clear();
              sessionStorage.clear();

              // Recargar pgina
              window.location.reload();
              return;
            }
          } catch (error) {
            console.error(' Error verificando membresas:', error);
          }
        }

        setCurrentUser(user);
        setOrganizationId(orgId);
        setIsAuthenticated(true);

        console.log(' useAuthUser - Usuario cargado:', {
          email: user.email,
          organizationId: orgId
        });

      } catch (error) {
        console.error(' Error en useAuthUser:', error);
        setCurrentUser(null);
        setOrganizationId(null);
        setIsAuthenticated(false);
      } finally {
        setIsLoading(false);
        isLoadingRef.current = false;
      }
    };

    loadUser();

    // Suscribirse a cambios de autenticacin
    const { data: authListener } = supabaseService.supabase.auth.onAuthStateChange((event, session) => {
      console.log(' useAuthUser - Cambio de autenticacin:', event);

      if (event === 'SIGNED_OUT') {
        setCurrentUser(null);
        setOrganizationId(null);
        setIsAuthenticated(false);
        lastUserEmail.current = null;
      } else if (event === 'SIGNED_IN' && session) {
        loadUser();
      }
    });

    return () => {
      authListener?.subscription?.unsubscribe();
    };
  }, []);

  /**
   * Recarga manualmente el usuario y organizacin desde Supabase
   *
   * til cuando se necesita forzar una actualizacin del estado de autenticacin,
   * por ejemplo despus de cambiar de organizacin o actualizar el perfil.
   *
   * @async
   * @function reloadUser
   * @returns {Promise<void>}
   *
   * @example
   * const { reloadUser } = useAuthUser();
   *
   * // Despus de cambiar organizacin
   * await switchOrganization(newOrgId);
   * await reloadUser(); // Actualizar estado
   */
  const reloadUser = async () => {
    isLoadingRef.current = false;
    lastUserEmail.current = null;

    const user = supabaseService.getCurrentUser();
    const orgId = supabaseService.getCurrentOrganization();

    setCurrentUser(user);
    setOrganizationId(orgId);
    setIsAuthenticated(!!user && !!user.email);
  };

  return {
    // Estado
    currentUser,
    organizationId,
    isLoading,
    isAuthenticated,

    // Funciones
    reloadUser,

    // Helpers
    userEmail: currentUser?.email || null,
    userId: currentUser?.id || null,
  };
};

export default useAuthUser;
